@media all {
    .aa-survey .panel-available-survey .panel-body {
        height: 150px;
    }

    .aa-survey-footer {
        border-top: 1px solid rgb(236 240 241);
        font-size: 85%;
        margin-top: 1rem;
        padding-top: 1rem;
    }

    .template-dark-mode .aa-survey-footer {
        border-color: rgb(70 69 69);
    }

    .aa-survey-survey-name {
        width: 250px;
    }

    .aa-survey-survey-actions {
        width: 120px;
    }

    .btn-aa-survey {
        margin: 2px;
    }
}
